import React from 'react';
import anime from "animejs";

export default function GradientBG(props) {
	const {direction = 'up'} = props;
	
	React.useEffect( () => {
		anime({
			targets: ".gradient-bg",
			height: [0, '100%'],
			easing: 'easeInOutQuad',
			duration: 200,
		});
	});
	
	return (
    <div className={`gradient-bg ${direction}`}>
    </div>
  );
}
